    VEHICLE_INQUIRY_STATUS_IN_PROGRESS,
    VEHICLE_INQUIRY_STATUS_SENT_KP,
    VEHICLE_INQUIRY_STATUS_CLOSED_SUCCESSFULLY,
    VEHICLE_INQUIRY_STATUS_CLOSED_NOT_SUCCESSFULLY,
