import { Entity } from '~/application';
import { ADMIN_ROLE_ADMIN, ADMIN_ROLE_MANAGER } from '../roles';

import { fetchItemsFn, getItemsFn, updateItemsFn, createItemsFn } from '../api';
import { RegionSelectField } from '../components/RegionSelectField';
import { CitySelectField } from '../components/CitySelectField';

export const TYPE_OFFER = 10;
export const TYPE_NEWS = 20;

export const CUSTOMER_STATUS_ACTIVE = 10;
export const CUSTOMER_STATUS_BLOCKED = 20;
export const CUSTOMER_STATUS_REGISTRATION = 30;

export default (host: string) =>
  new Entity({
    title: '',
    editable: true,
    viewable: true,
    roles: {
      list: [ADMIN_ROLE_ADMIN.toString(), ADMIN_ROLE_MANAGER.toString()],
      update: [ADMIN_ROLE_ADMIN.toString()],
      create: [ADMIN_ROLE_ADMIN.toString()],
    },
    api: {
      list: {
        url: `${host}/admin/v1/customer`,
        method: 'get',
      },
      update: {
        url: `${host}/admin/v1/customer`,
        method: 'put',
      },
      create: {
        url: `${host}/admin/v1/customer`,
        method: 'post',
      },
      get: {
        url: `${host}/admin/v1/customer`,
        method: 'get',
      },
    },
    menu: {
      enabled: true,
      label: '',
      url: '/customer',
    },
    fields: [
      {
        name: 'name',
        type: 'string',
        label: '...',
        hideInEdit: true,
        hideInList: true,
        filterable: true,
      },
      {
        name: 'firstName',
        type: 'string',
        label: '',
        required: true,
        filterable: false,
      },
      {
        name: 'lastName',
        type: 'string',
        label: '',
        required: true,
        filterable: false,
      },
      {
        name: 'middleName',
        type: 'string',
        label: '',
        required: true,
        filterable: false,
      },
      {
        name: 'status',
        type: 'select',
        label: '',
        required: true,
        sortable: true,
        filterable: false,
        options: {
          [CUSTOMER_STATUS_ACTIVE]: '',
          [CUSTOMER_STATUS_BLOCKED]: '',
          [CUSTOMER_STATUS_REGISTRATION]: ' ',
        },
      },
      {
        name: 'phone',
        type: 'phone',
        label: '',
        required: true,
        filterable: true,
      },
      {
        name: 'email',
        type: 'string',
        label: 'E-mail',
        required: true,
        filterable: true,
      },
      {
        name: 'birthday',
        type: 'date',
        label: ' ',
        filterable: false,
        hideInList: true,
      },
      {
        name: 'company',
        type: 'string',
        label: '',
        filterable: false,
        hideInList: true,
      },
      {
        name: 'website',
        type: 'string',
        label: 'WWW',
        filterable: false,
        hideInList: true,
      },
      {
        name: 'regionId',
        type: 'custom', // TODO: should be reference field
        label: '/',
        hideInList: true,
        component: RegionSelectField,
        options: {
          getRegionsUrl: `${host}/admin/v1/location/region`,
        },
      },
      {
        name: 'cityId',
        type: 'custom', // TODO: should be reference field
        label: '',
        hideInList: true,
        component: CitySelectField,
        options: {
          getRegionsUrl: `${host}/admin/v1/location/region`,
        },
      },
    ],
    fetchItemsFn,
    getItemsFn,
    updateItemsFn,
    createItemsFn,
  });
